@import "../utils/mixins";

.page {
    @apply relative;
    @apply w-full;
    min-height: 500px;
    max-width: 1920px;
    left: 50%;
    transform: translateX(-50%);
    overflow: hidden;
    &-title {
        @apply font-bold;
    }
    &-caption {
        @apply font-medium;
    }
}

.section {
    @apply relative;
    &-info {
        @apply relative;
        margin-bottom: vw(80px, 750px);
        &:last-child {
            margin-bottom: 0;
            > *:last-child {
                margin-bottom: 0 !important;
            }
        }
        @include break-point(tablet) {
            margin-bottom: 80px;
        }
        @include break-point(desktop) {
            margin-bottom: rateValue(80px);
        }

        &-title {
            font-weight: 400;
            margin-bottom: vw(30px, 750px);
            @include break-point(tablet) {
                margin-bottom: 20px;
            }
            @include break-point(desktop) {
                margin-bottom: rateValue(24px);
            }
        }
        &-subtitle {
            margin-bottom: vw(12px, 750px);
            @include break-point(tablet) {
                margin-bottom: 12px;
            }
            @include break-point(desktop) {
                margin-bottom: rateValue(12px);
            }
        }
        &-desc {
            margin-bottom: vw(40px, 750px);
            @include break-point(tablet) {
                margin-bottom: 40px;
            }
            @include break-point(desktop) {
                margin-bottom: rateValue(80px);
            }
        }
        &-list {
            padding-left: vw(40px, 750px);
            @include break-point(tablet) {
                padding-left: 20px;
            }
            @include break-point(desktop) {
            }

            > li {
                &:not(:last-child) {
                    margin-bottom: vw(40px, 750px);
                    @include break-point(tablet) {
                        margin-bottom: 40px;
                    }
                    @include break-point(desktop) {
                        margin-bottom: rateValue(40px);
                    }
                }

                ul,
                ol {
                    margin-top: vw(40px, 750px);
                    @include break-point(tablet) {
                        margin-top: 20px;
                    }
                    @include break-point(desktop) {
                        margin-top: rateValue(20px);
                    }
                    > li {
                        margin-bottom: 0 !important;
                        @include break-point(tablet) {
                        }
                        @include break-point(desktop) {
                        }
                    }
                }
            }
        }
        .img-caption {
            margin-bottom: vw(60px, 750px);
            @include break-point(tablet) {
                margin-bottom: 30px;
            }
            @include break-point(desktop) {
            }
        }
    }
}

.container {
    @apply relative;
    @apply w-full;
    @apply mx-auto;
    // border: 1px solid green;
    &.max-w-none {
        max-width: none !important;
    }
    /*  @include break-point(tablet) {
        padding: 0 78px;
    }
    @include break-point(desktop) {
        padding: 0 7%;
    } */
}
