@import "../utils/mixins.scss";

#aboutPage {
    .page-head {
        @apply relative;
        margin-bottom: vw(64px, 750px);
        z-index: 2;
        .kv {
            @apply relative;
            @apply w-full;
            padding-top: vw(615px, 750px);
            @include break-point(tablet) {
                width: 1200px;
                left: 50%;
                transform: translateX(-50%);
                padding-top: 440px;
            }
            @include break-point(tablet) {
                width: 1920px;
                left: 50%;
                transform: translateX(-50%);
                padding-top: rateValue(180px);
            }

            &-arrow {
                @include aspect-ratio(2499, 3406);
                @include image2x(
                    url(../../assets/images/graphics/arrow.png),
                    url(../../assets/images/graphics/arrow@2x.png),
                    cover
                );
                @apply absolute;
                width: vw(2010px, 750px);
                top: vw(200px, 750px);
                left: -204%;
                @include break-point(tablet) {
                    width: 1850px;
                    top: 125px;
                    left: -1137px;
                }
                @include break-point(desktop) {
                    width: rateValue(2500px, 0.65);
                    top: rateValue(150px);
                    left: -807px;
                }
            }
            &-model {
                @include aspect-ratio(1352, 1200);
                @include image2x(
                    url(../../assets/images/about/model.png),
                    url(../../assets/images/about/model@2x.png),
                    contain
                );

                &-group {
                    @apply relative;
                    width: vw(735px, 750px);
                    left: 50%;
                    transform: translateX(-30%);
                    z-index: 1;
                    @include break-point(tablet) {
                        width: 676px;
                        left: 50%;
                        transform: translateX(-5%);
                    }
                    @include break-point(desktop) {
                        width: rateValue(940px, 0.65);
                        left: 50%;
                        transform: translateX(18%);
                    }
                }
                &-dot {
                    @apply absolute;
                    width: vw(175px, 750px);
                    left: 50%;
                    top: -5%;
                    transform: translateX(27%);
                    @include break-point(tablet) {
                        width: 164px;
                        left: 50%;
                        top: -5%;
                        transform: translateX(57%);
                    }
                    @include break-point(desktop) {
                        width: rateValue(225px, 0.65);
                        left: 50%;
                        top: -5%;
                        transform: translateX(-235%);
                    }
                }
            }
        }
        .triangle-primary {
            @apply absolute;
            @include aspect-ratio(800px, 1390px, absolute);
            width: vw(860px, 750px);
            top: 0;
            left: 0;
            @include break-point(tablet) {
                width: 1030px;
            }
            @include break-point(desktop) {
                width: 1080px;
            }
        }
        .triangle-gray {
            @apply absolute;
            @include aspect-ratio(1100px, 1900px, absolute);
            width: vw(1155px, 750px);
            top: 0;
            left: 0;
            @include break-point(tablet) {
                width: 1300px;
            }
            @include break-point(desktop) {
                width: 1510px;
            }
        }
        .page-title {
            @apply absolute;
            @apply whitespace-nowrap;
            line-height: 0.82;
            letter-spacing: vw(-2.3px, 750px);
            @include break-point(tablet) {
                letter-spacing: -2.13px;
            }
            @include break-point(desktop) {
                font-size: rateValue(120px, 0.65);
            }
        }
    }
    .intro-section {
        @apply relative;
        z-index: 3;

        .container {
            @apply relative;
            z-index: 2;
        }

        .section-info-outer {
            @include break-point(tablet) {
                padding-left: 20%;
            }
            @include break-point(desktop) {
                padding-left: 40%;
            }
        }
        .title-footprint {
            margin-bottom: vw(60px, 750px);
            @include break-point(tablet) {
                margin-bottom: 40px;
            }
            @include break-point(desktop) {
                margin-bottom: rateValue(60px);
            }
        }
        .history {
            &:not(:last-child) {
                margin-bottom: vw(80px, 750px);
                @include break-point(tablet) {
                    margin-bottom: 80px;
                }
                @include break-point(desktop) {
                    margin-bottom: rateValue(130px);
                }
            }
            &-title {
                @apply text-gray;
                font-weight: 400;
                letter-spacing: -1.5px;
                margin-bottom: vw(60px, 750px);
                @include break-point(tablet) {
                    letter-spacing: -1.5px;
                    margin-bottom: 30px;
                }
                @include break-point(desktop) {
                    letter-spacing: rateValue(-2.5px);
                    margin-bottom: rateValue(40px);
                }
            }
            &-year {
                @apply text-primary;
                flex-grow: 0;
                font-weight: 400;
                margin-bottom: vw(20px, 750px);
                @include break-point(tablet) {
                    margin-right: 30px;
                    margin-bottom: 0;
                }
                @include break-point(desktop) {
                    margin-right: rateValue(44px);
                }
            }
            &-month {
                @apply block;
                @apply absolute;
                left: vw(40px, 750px);
                font-weight: 400;
                @include break-point(tablet) {
                    left: 30px;
                }
                @include break-point(desktop) {
                    left: rateValue(44px);
                }
            }

            &-list {
                @apply text-gray-dark;
                flex-grow: 1;
                font-weight: 400;
                @include break-point(tablet) {
                }
                @include break-point(desktop) {
                }

                &-group {
                    margin-bottom: vw(36px, 750px);
                    @include break-point(tablet) {
                        margin-bottom: 0;
                    }
                }

                li {
                    @apply flex;
                    @apply relative;
                    padding-left: vw(160px, 750px);
                    padding-bottom: vw(40px, 750px);
                    @include break-point(tablet) {
                        padding-left: 105px;
                        padding-bottom: 30px;
                    }
                    @include break-point(desktop) {
                        padding-left: rateValue(160px);
                        padding-bottom: rateValue(60px);
                    }

                    &::before {
                        @apply absolute;
                        @apply block;
                        @apply bg-primary;
                        @apply rounded-full;
                        content: "";
                        width: vw(20px, 750px);
                        height: vw(20px, 750px);
                        left: 0;
                        top: 8px;
                        @include break-point(tablet) {
                            @apply absolute;
                            @apply block;
                            @apply bg-primary;
                            @apply rounded-full;
                            content: "";
                            width: rateValue(20px);
                            height: rateValue(20px);
                            left: rateValue(-10px);
                            top: 6px;
                        }
                        @include break-point(desktop) {
                            width: rateValue(20px);
                            height: rateValue(20px);
                            left: rateValue(-10px);
                            top: 6px;
                        }
                    }
                    &::after {
                        @apply absolute;
                        @apply block;
                        @apply bg-primary;
                        @apply left-0;
                        top: 8px;
                        left: vw(10px, 750px);
                        content: "";
                        width: 1px;
                        height: 100%;
                        @include break-point(tablet) {
                            @apply absolute;
                            @apply block;
                            @apply bg-primary;
                            @apply left-0;
                            top: 6px;
                            left: 0;
                            content: "";
                            width: 1px;
                            height: 100%;
                        }
                        @include break-point(desktop) {
                            top: 6px;
                            left: -1px;
                            content: "";
                            width: 1px;
                            height: 100%;
                        }
                    }

                    p {
                        font-weight: 400;
                    }
                }
            }

            &-list-group {
                @apply block;
                @include break-point(tablet) {
                    @apply flex;
                }
                @include break-point(desktop) {
                }
                &:last-child {
                    .history-list {
                        li {
                            &:last-child {
                                padding-bottom: 0;
                                &::after {
                                    content: none;
                                }
                            }
                        }
                    }
                }
            }
        }
        .dot {
            &-1 {
                @apply absolute;
                display: none;
                @include break-point(desktop) {
                    @apply block;
                    width: rateValue(570px);
                    top: 5%;
                    left: 50%;
                    transform: translateX(250%);
                }
            }
            &-2 {
                @apply absolute;
                display: none;
                @include break-point(desktop) {
                    @apply block;
                    width: rateValue(980px);
                    left: 50%;
                    top: 50%;
                    transform: translate(-155%, 20%);
                }
            }
            &-3 {
                @apply absolute;
                display: none;
                @include break-point(desktop) {
                    @apply block;
                    width: rateValue(180px);
                    left: 50%;
                    transform: translateX(-380%);
                    top: 16%;
                }
            }
        }
    }
    .page-footer {
        @apply relative;
        height: vw(2410px, 750px);
        z-index: 1;
        @include break-point(tablet) {
            width: 1200px;
            height: 2540px;
            left: 50%;
            transform: translateX(-50%);
        }
        @include break-point(desktop) {
            width: 1920px;
            height: rateValue(1976px, 0.65);
            left: 50%;
            transform: translateX(-50%);
        }
        .triangle-gray {
            @include aspect-ratio(2410px, 4171px, absolute);
            @include image2x(
                url(../../assets/images/about/triangle-gray.png),
                url(../../assets/images/about/triangle-gray.png),
                cover
            );
            @apply block;
            width: vw(1647px, 750px);
            bottom: 0;
            right: 0;
            @include break-point(tablet) {
                width: 1890px;
                bottom: 0;
                right: -20px;
            }
            @include break-point(desktop) {
                width: rateValue(2100px, 0.65);
                bottom: 0;
                right: 0;
            }
        }
        .triangle-white {
            @include aspect-ratio(536px, 887px, absolute);
            width: vw(536px, 750px);
            bottom: 0;
            right: 0;
            @include break-point(tablet) {
                width: 620px;
                bottom: 0;
                right: -8px;
            }
            @include break-point(desktop) {
                @apply invisible;
            }
        }
        .photos-group {
            @apply w-full;
            @apply h-full;
            left: 0;
            bottom: 0;
            @include break-point(tablet) {
            }
            @include break-point(desktop) {
            }
        }
        .photos {
            @include aspect-ratio(750, 2129);
            @include image2x(
                url(../../assets/images/about/xs/photos.png),
                url(../../assets/images/about/xs/photos@2x.png),
                cover
            );
            @apply w-full;
            position: absolute;
            left: 0;
            bottom: vw(234px, 750px);
            @include break-point(tablet) {
                @include aspect-ratio(1200, 2131);
                @include image2x(
                    url(../../assets/images/about/sm/photos.png),
                    url(../../assets/images/about/sm/photos@2x.png),
                    cover
                );
                width: 1200px;
                left: 0;
                bottom: -200px;
            }
            @include break-point(desktop) {
                @include aspect-ratio(1275, 1871);
                @include image2x(
                    url(../../assets/images/about/md/photos.png),
                    url(../../assets/images/about/md/photos@2x.png),
                    cover
                );
                width: rateValue(1275px, 0.65);
                position: absolute;
                left: 50%;
                bottom: 0;
                transform: translate(-20.5%, 0);
            }
        }
        .text-backlog {
            @apply absolute;
            color: #ebebeb;
            font-size: vw(160px, 750px);
            font-weight: 600;
            line-height: 1;
            letter-spacing: -4px;
            left: -12%;
            top: 16%;
            transform: translate(0) rotate(-60deg);
            font-family: "Roboto", sans-serif !important;
            @include break-point(tablet) {
                font-size: 160px;
                font-weight: 600;
                line-height: 1;
                letter-spacing: -4px;
                left: 11%;
                top: 20%;
                transform: translate(0) rotate(-60deg);
            }
            @include break-point(desktop) {
                font-size: rateValue(200px, 0.65);
                font-weight: 600;
                line-height: 1;
                letter-spacing: -4px;
                left: auto;
                top: -74%;
                right: -3.5%;
                transform: translate(0) rotate(-60deg);
            }
        }
        .text-sprint {
            @apply absolute;
            color: #c1c1c1;
            font-size: vw(156px, 750px);
            font-weight: 600;
            line-height: 1;
            letter-spacing: -4px;
            right: 8%;
            bottom: 11%;
            transform: translate(0) rotate(-60deg);
            font-family: "Roboto", sans-serif !important;
            @include break-point(tablet) {
                font-size: 156px;
                font-weight: 600;
                line-height: 1;
                letter-spacing: -4px;
                right: 24%;
                bottom: 9%;
                transform: translate(0) rotate(-60deg);
            }
            @include break-point(desktop) {
                font-size: rateValue(200px, 0.65);
                font-weight: 600;
                line-height: 1;
                letter-spacing: -4px;
                right: 8.5%;
                bottom: 35%;
                transform: translate(0) rotate(-60deg);
            }
        }
        .io {
            @apply absolute;
            &-1 {
                width: vw(500px, 750px);
                left: -22%;
                bottom: 1%;
                transform: translate(0) rotate(-60deg);
                @include break-point(tablet) {
                    width: 400px;
                    left: 13%;
                    bottom: 3%;
                    transform: translate(0) rotate(-60deg);
                }
                @include break-point(desktop) {
                    width: rateValue(660px, 0.65);
                    left: 29%;
                    bottom: auto;
                    top: 38%;
                    transform: translate(0) rotate(-60deg);
                }
            }
            &-2 {
                width: vw(1200px, 750px);
                left: -3%;
                bottom: 26%;
                transform: translate(0) rotate(-60deg);
                @include break-point(tablet) {
                    width: 1200px;
                    left: 16%;
                    bottom: 23%;
                    transform: translate(0) rotate(-60deg);
                }
                @include break-point(desktop) {
                    width: rateValue(1400px, 0.65);
                    left: 66%;
                    bottom: 68%;
                    transform: translate(0) rotate(-60deg);
                }
            }
            &-3 {
                width: vw(500px, 750px);
                left: -26%;
                top: 49%;
                transform: translate(0) rotate(-60deg);
                @include break-point(tablet) {
                    width: 440px;
                    left: 7%;
                    top: 51%;
                    transform: translate(0) rotate(-60deg);
                }
                @include break-point(desktop) {
                    width: rateValue(760px, 0.65);
                    left: 34.4%;
                    top: 51%;
                    transform: translate(0) rotate(-60deg);
                }
            }
        }
    }
}
:lang(en) {
    #aboutPage {
        .page-title {
            font-size: vw(90px, 750px);
            left: 50%;
            top: 50%;
            transform: translate(-70.5%, -250%) rotate(-60deg);
            @include break-point(tablet) {
                font-size: 85px;
                transform: translate(-85%, -175%) rotate(-60deg);
            }
            @include break-point(desktop) {
                transform: translate(-78.5%, -58%) rotate(-60deg);
            }
        }
    }
}
:lang(zh-hant) {
    #aboutPage {
        .page-title {
            font-size: vw(82px, 750px);
            left: 50%;
            top: 50%;
            transform: translate(-70.5%, -313%) rotate(-60deg);
            @include break-point(tablet) {
                font-size: 72px;
                transform: translate(-97%, -175%) rotate(-60deg);
            }
            @include break-point(desktop) {
                transform: translate(-84%, -90%) rotate(-60deg);
            }
        }
    }
}
