@import "../utils/mixins";

.address {
    font-style: normal;
    margin: vw(-20px, 750px);
    @include break-point(tablet) {
        font-style: normal;
        margin: vw(-5px, 750px);
    }
    @include break-point(desktop) {
        align-items: center;
        font-style: normal;
        margin: rateValue(-10px);
    }
    > div {
        padding: vw(20px, 750px);
        @include break-point(tablet) {
            padding: vw(5px, 750px);
        }
        @include break-point(desktop) {
            padding: rateValue(10px);
        }
    }
    &-info {
        &-label {
            @apply block;
            font-weight: 500;
            margin-bottom: vw(5px, 750px);
            @include break-point(tablet) {
                @apply inline-block;
                margin-bottom: 0;
            }
        }
        a {
            @apply block;
            text-decoration: underline;
            font-weight: 500;
            @include break-point(tablet) {
                @apply inline-block;
                text-decoration: none;
            }
            @include break-point(desktop) {
            }
            &:hover {
                color: theme("colors.primary");
                text-decoration: underline;
            }
        }

        &-email {
            @apply flex;
            @apply items-center;
            @apply flex-wrap;
        }
    }

    .email-text {
        @apply block;
        @include aspect-ratio(286, 28);
        @include image2x(
            url(../../assets/images/graphics/email-xs.png),
            url(../../assets/images/graphics/email-xs.png),
            contain
        );

        width: 45%;

        @include break-point(tablet) {
            @include image2x(
                url(../../assets/images/graphics/email-sm.png),
                url(../../assets/images/graphics/email-sm.png),
                contain
            );
            width: 205px;
            transform: translateY(1px);
            margin-left: 3px;
        }
        @include break-point(desktop) {
            @include image2x(
                url(../../assets/images/graphics/email-md.png),
                url(../../assets/images/graphics/email-md.png),
                contain
            );
            width: 135px;
            // transform: translateY(1px);
            margin-left: 3px;
        }
    }
}
