@import "../utils/mixins.scss";
.footer {
    @apply bg-black;
    @apply text-white;
    padding: vw(40px, 750px) 0;
    @include break-point(tablet) {
        padding: 40px 0;
    }
    @include break-point(desktop) {
        padding: rateValue(40px) 0;
    }

    .address {
        margin: vw(-20px, 750px);
        @include break-point(tablet) {
            margin: -10px;
        }
        @include break-point(desktop) {
            margin: rateValue(-10px) rateValue(-20px);
        }
        > div {
            padding: vw(20px, 750px);
            @include break-point(tablet) {
                padding: 10px;
            }
            @include break-point(desktop) {
                padding: rateValue(10px) rateValue(20px);
            }
        }
        .copyright {
            margin-top: vw(40px, 750px);
            @include break-point(tablet) {
                margin-top: 40px;
            }
            @include break-point(desktop) {
                @apply text-center;
                margin-top: 0;
            }
        }
    }
    .copyright {
        font-size: 3.5vw;
        font-weight: 500;
        @include break-point(tablet) {
            font-size: 20px;
        }
        @include break-point(desktop) {
            font-size: rateValue(26px);
        }
    }
}
