body,
html {
    @apply relative;
    @apply antialiased;
    width: 100%;
    overflow: hidden;
    overflow-y: auto;
    user-select: none;
    * {
        box-sizing: border-box !important;
        &:focus {
            outline: 0 !important;
        }
    }
}

img {
    @apply relative;
    display: block;
    pointer-events: none;
    max-width: none;
    width: 100%;
    height: auto;
}

a {
    display: inline-block;
}

button {
    background-color: transparent;
    border: none;
}

section {
    position: relative;
}
