@import "../utils/mixins";

#servicePage {
    .page-head {
        margin-bottom: vw(196px, 750px);
        @include break-point(tablet) {
            margin-bottom: 190px;
        }
        @include break-point(desktop) {
            margin-bottom: 190px;
        }

        .bg {
            @include aspect-ratio(750, 1264);
            @include image2x(
                url(../../assets/images/service/xs/kv.jpg),
                url(../../assets/images/service/xs/kv@2x.jpg)
            );
            @apply relative;
            @apply w-full;

            @include break-point(tablet) {
                @include aspect-ratio(1199, 955);
                @include image2x(
                    url(../../assets/images/service/sm/kv.jpg),
                    url(../../assets/images/service/sm/kv@2x.jpg)
                );
            }
            @include break-point(desktop) {
                @include aspect-ratio(1364, 960);
                @include image2x(
                    url(../../assets/images/service/md/kv.jpg),
                    url(../../assets/images/service/md/kv@2x.jpg)
                );
                width: 1364px;
            }

            &-group {
                @apply relative;
                @apply w-full;
                @include break-point(tablet) {
                    width: 1200px;
                    left: 50%;
                    transform: translateX(-50%);
                }
                @include break-point(desktop) {
                    width: 1920px;
                }
            }
            &-model {
                @include aspect-ratio(1058, 1128);
                @include image2x(
                    url(../../assets/images/service/model.png),
                    url(../../assets/images/service/model@2x.png)
                );
                @apply absolute;
                width: vw(850px, 750px);
                left: -42%;
                bottom: -23%;
                @include break-point(tablet) {
                    width: 640px;
                    left: 50px;
                    bottom: -225px;
                }
                @include break-point(desktop) {
                    width: rateValue(1258px);
                    left: 50%;
                    top: 50%;
                    bottom: auto;
                    transform: translate(-65%, -10%);
                }
            }
            &-triangle {
                &.triangle-gray {
                    @include aspect-ratio(830px, 1470px, absolute);
                    width: vw(830px, 750px);
                    top: 0;
                    left: 0;
                    @include break-point(tablet) {
                        width: 926px;
                    }
                    @include break-point(desktop) {
                        width: 71.25%;
                    }
                }
                &.triangle-primary {
                    @include aspect-ratio(560px, 976px, absolute);
                    width: vw(560px, 750px);
                    top: 0;
                    left: 0;
                    @include break-point(tablet) {
                        width: 730px;
                    }
                    @include break-point(desktop) {
                        width: 54.5%;
                    }
                }
            }
            &-io {
                @apply absolute;
                width: vw(1200px, 750px);
                left: -90%;
                top: 53%;
                transform: translate(0, 0) rotate(-60deg);
                transform-origin: center center;
                @include break-point(tablet) {
                    width: 800px;
                    left: 50%;
                    top: 50%;
                    transform: translate(-103%, 786%) rotate(-60deg);
                    transform-origin: center center;
                }
                @include break-point(desktop) {
                    @apply block;
                    @apply absolute;
                    width: 800px;
                    left: 50%;
                    top: 50%;
                    transform: translate(-101.6%, 1200%) rotate(-60deg);
                    transform-origin: center center;
                    z-index: 2;
                }
            }
            &-arrow {
                @include aspect-ratio(340, 1066);
                @include image2x(
                    url(../../assets/images/service/xs/arrow.png),
                    url(../../assets/images/service/xs/arrow@2x.png),
                    contain
                );
                @apply absolute;
                width: vw(340px, 750px);
                left: 0;
                bottom: 0;
                @include break-point(tablet) {
                    @include aspect-ratio(559, 1585);
                    @include image2x(
                        url(../../assets/images/service/sm/arrow.png),
                        url(../../assets/images/service/sm/arrow@2x.png),
                        contain
                    );
                    @apply absolute;
                    width: 560px;
                    left: 0;
                    bottom: -768px;
                }
                @include break-point(desktop) {
                    @include aspect-ratio(2499, 3406);
                    @include image2x(
                        url(../../assets/images/graphics/arrow.png),
                        url(../../assets/images/graphics/arrow@2x.png),
                        cover
                    );
                    @apply absolute;
                    width: rateValue(2160px, 0.65);
                    left: -528.5px;
                    top: rateValue(265px);
                    bottom: auto;
                    z-index: 1;
                }
            }
        }
        .page-title {
            @apply absolute;
            @apply whitespace-nowrap;
            font-size: vw(100px, 750px);
            line-height: 1;
            letter-spacing: -2.48px;
            top: 50%;
            left: 50%;
            transform: translate(-40%, -220%) rotate(-60deg);
            transform-origin: center center;
            @include break-point(tablet) {
                font-size: 75px;
                top: 50%;
                left: 50%;
                transform: translate(-40%, -220%) rotate(-60deg);
            }
            @include break-point(desktop) {
                font-size: rateValue(90px, 0.65);
                top: 50%;
                left: 50%;
                transform: translate(-12%, -250%) rotate(-60deg);
            }
        }
    }

    .intro-section {
        @apply relative;
        margin-bottom: vw(300px, 750px);
        @include break-point(tablet) {
            margin-bottom: 100px;
        }
        @include break-point(desktop) {
            margin-bottom: rateValue(390px);
        }

        .section-info {
            z-index: 2;
            @include break-point(desktop) {
                padding-left: 30%;
            }
        }
        .dot-yellow {
            @apply absolute;
            width: vw(860px, 750px);
            top: 50%;
            left: -50%;
            transform: translateY(-12%);
            @include break-point(tablet) {
                @apply absolute;
                width: 860px;
                top: auto;
                left: -160px;
                bottom: -300px;
                transform: translateY(0);
            }
            @include break-point(desktop) {
                @apply absolute;
                width: rateValue(860px);
                top: auto;
                left: 50%;
                bottom: 0;
                transform: translate(-105%, 20%);
            }
        }
        .triangle-primary {
            @include aspect-ratio(750px, 1300px, absolute);
            @include image2x(
                url(../../assets/images/service/triangle-primary.png),
                url(../../assets/images/service/triangle-primary.png)
            );
            @apply absolute;
            width: vw(395px, 750px);
            right: -1px;
            bottom: vw(-302px, 750px);
            @include break-point(tablet) {
                display: none;
            }
            @include break-point(desktop) {
                @include aspect-ratio(750px, 1300px, absolute);
                @apply block;
                @apply absolute;
                width: 750px;
                right: -1px;
                bottom: vw(-302px, 750px);
            }
        }
        .io {
            @apply absolute;
            width: vw(850px, 750px);
            right: -57%;
            bottom: 3%;
            transform: rotate(-60deg);
            transform-origin: center center;
            opacity: 0.4;
            @include break-point(tablet) {
                display: none;
            }
            @include break-point(desktop) {
                @apply block;
                @apply absolute;
                width: rateValue(1260px);
                right: -18%;
                bottom: 3%;
            }
        }
    }
}

:lang(en) {
    #servicePage {
        .page-head {
            .page-title {
                top: 50%;
                left: 50%;
                transform: translate(-40%, -220%) rotate(-60deg);
                @include break-point(tablet) {
                    top: 50%;
                    left: 50%;
                    transform: translate(-40%, -220%) rotate(-60deg);
                }
                @include break-point(desktop) {
                    top: 50%;
                    left: 50%;
                    transform: translate(-18%, -220%) rotate(-60deg);
                }
            }
        }
    }
}
:lang(zh-hant) {
    #servicePage {
        .page-head {
            .page-title {
                top: 50%;
                left: 50%;
                transform: translate(-32%, -220%) rotate(-60deg);
                @include break-point(tablet) {
                    top: 50%;
                    left: 50%;
                    transform: translate(-40%, -220%) rotate(-60deg);
                }
                @include break-point(desktop) {
                    top: 50%;
                    left: 50%;
                    transform: translate(-18%, -220%) rotate(-60deg);
                }
            }
        }
    }
}
