@import "../utils/variables";
@import "../utils/functions";
@import "../utils/mixins";

.header {
    @apply relative;
    @apply flex;
    @apply items-center;
    @apply fixed;
    @apply w-full;
    height: $header-height-xs;
    top: 0;
    left: 0;
    &::before {
        @apply block;
        @apply absolute;
        @apply w-full;
        @apply h-full;
        @apply bg-primary;
        content: "";
        top: 0;
        left: 0;
        transition: top 0.3s ease-in-out;
        @include break-point(tablet) {
            top: -100%;
        }
        @include break-point(desktop) {
        }
    }
    &.sticky {
        &::before {
            top: 0;
        }
    }
    @include break-point(tablet) {
        height: $header-height-sm;
    }
    @include break-point(desktop) {
        height: $header-height-md;
    }

    // Collapse
    &-collapse {
        @apply absolute;
        @apply flex;
        @apply justify-end;
        @apply w-full;
        @apply opacity-0;
        @apply transition-opacity;
        @apply duration-300;
        height: 100vh;
        top: 0;
        right: 0;
        pointer-events: none;
        &.active {
            @apply opacity-100;
            .header-collapse-overlay {
                @apply opacity-100;
                @apply transition-opacity;
                @apply duration-150;
                @apply delay-100;
                @apply pointer-events-auto;
            }
            .header-collapse-inner {
                transform: translateX(0);
                @apply transition-transform;
                @apply duration-500;
                @apply delay-150;
            }
        }
        @include break-point(tablet) {
        }
        @include break-point(desktop) {
            @apply self-stretch;
            @apply justify-start;
            @apply items-center;
            @apply relative;
            @apply w-full;
            @apply h-auto;
            @apply opacity-100;
            @apply transition-none;
        }

        &-overlay {
            display: block;
            @apply absolute;
            @apply w-full;
            @apply h-full;
            @apply bg-black;
            @apply bg-opacity-80;
            @apply top-0;
            @apply left-0;
            @apply opacity-0;
            @include break-point(tablet) {
            }
            @include break-point(desktop) {
                display: none;
            }
        }
        &-inner {
            @apply flex;
            @apply flex-col-reverse;
            @apply justify-end;
            @apply relative;
            @apply bg-primary;
            @apply w-full;
            @apply pointer-events-auto;
            padding-top: $header-height-xs;
            transform: translateX(100%);

            @include break-point(tablet) {
                padding-top: $header-height-sm;
                width: 400px;
            }
            @include break-point(desktop) {
                @apply self-stretch;
                @apply flex-row;
                @apply items-center;
                @apply w-full;
                @apply bg-transparent;
                @apply opacity-100;
                @apply transition-none;
                padding-top: 0;
                transform: translateX(0);
            }
        }
        &-toggle {
            @apply flex;
            @apply justify-center;
            @apply items-center;
            @apply absolute;
            width: $header-height-xs;
            height: $header-height-xs;
            right: 0;
            top: 0;
            transition: all 0.3s ease-out;
            @include break-point(tablet) {
                width: $header-height-sm;
                height: $header-height-sm;
            }
            @include break-point(desktop) {
                display: none;
            }
            .icon {
                @apply relative;
                width: 26px;
                height: 18px;
                span {
                    @apply block;
                    @apply bg-white;
                    @apply absolute;
                    width: 100%;
                    height: 2px;
                    transition: all 0.3s ease-out;
                    &:nth-child(1) {
                        top: 0;
                    }
                    &:nth-child(2) {
                        top: 50%;
                        transform: translateY(-50%);
                    }
                    &:nth-child(3) {
                        bottom: 0;
                    }
                }
            }
            &.active {
                .icon {
                    span {
                        &:nth-child(1) {
                            top: 50%;
                            transform: translateY(-50%) rotate(-45deg);
                            transform-origin: center center;
                        }
                        &:nth-child(2) {
                            opacity: 0;
                        }
                        &:nth-child(3) {
                            bottom: auto;
                            top: 50%;
                            transform: translateY(-50%) rotate(45deg);
                            transform-origin: center center;
                        }
                    }
                }
            }
        }
    }
    // LogoLink
    &-logo {
        @apply flex;
        @apply self-stretch;
        @apply relative;
        z-index: 1;
        padding: 0 20px;
        @include break-point(tablet) {
        }
        @include break-point(desktop) {
            padding: 0 30px;
        }
        img {
            @apply relative;
            width: 100px;
            @include break-point(tablet) {
                width: width(210px);
            }
            @include break-point(desktop) {
            }
        }
    }
    // Nav
    &-nav {
        @apply block;
        @apply flex-grow;
        @apply relative;
        @include break-point(tablet) {
        }
        @include break-point(desktop) {
            @apply self-stretch;
            @apply flex-grow-0;
        }
        ul {
            @apply block;
            @apply relative;
            padding: 20px 0;
            @include break-point(tablet) {
            }
            @include break-point(desktop) {
                @apply flex;
                @apply self-center;
                @apply flex-row;
                @apply items-center;
                @apply h-full;
                margin: 0;
                padding-right: 20px;
            }
        }
        a.nav-link {
            @apply w-full;
            @apply font-normal;
            @apply whitespace-nowrap;
            font-size: 20px;
            letter-spacing: -0.5px;
            padding: 15px 20px;
            pointer-events: all;
            color: white;
            &:hover {
                @apply text-primary;
                @apply bg-white;
            }
            @include break-point(tablet) {
            }
            @include break-point(desktop) {
                @apply text-white;
                @apply w-auto;
                font-size: rateValue(28px, 0.6);

                padding: 20px 15px;
                &:hover {
                    @apply text-white;
                    @apply bg-transparent;
                    text-decoration: underline;
                }
            }
        }
    }
    // Language
    &-languages {
        @apply flex;
        @apply items-center;
        @apply bg-black;
        @include break-point(desktop) {
            @apply rounded-l-full;
            @apply w-auto;
            margin-left: auto;
            padding: 0 20px 0 5px;
        }
        li {
            width: 50%;
        }
        a {
            @apply text-white;
            @apply font-normal;
            @apply text-center;
            @apply w-full;
            font-size: 20px;
            padding: 15px 20px;
            pointer-events: all;
            /* transition: color 0.3s ease-out; */

            &:hover {
                @apply text-primary;
            }
            @include break-point(desktop) {
                font-size: rateValue(28px, 0.6);
                padding: 4px 10px;
            }
        }
    }
}
