@import "../utils/mixins";

#familyPage {
    .page-head {
        @apply relative;
        @apply bg-gray-light;
        padding-top: vw(626px, 750px);
        z-index: 1;
        @include break-point(tablet) {
            padding-top: 500px;
        }
        @include break-point(desktop) {
            padding-top: rateValue(240px);
        }

        &::before {
            @apply block;
            @apply absolute;
            @apply w-full;
            @apply bg-primary;
            content: "";
            height: 85%;
            top: 0;
            left: 0;
            @include break-point(tablet) {
            }
            @include break-point(desktop) {
            }
        }
        .model {
            @include aspect-ratio(1406, 2008);
            @include image2x(
                url(../../assets/images/family/model.png),
                url(../../assets/images/family/model@2x.png),
                cover
            );
            @apply relative;
            width: vw(690px, 750px);
            left: 50%;
            transform: translateX(-50%);
            @include break-point(tablet) {
                width: 575px;
                left: 50%;
                transform: translateX(-50%);
            }
            @include break-point(desktop) {
                width: rateValue(700px, 0.65);
                left: 50%;
                transform: translateX(-80%);
            }
        }
        .dot-black {
            @apply absolute;
            width: vw(320px, 750px);
            left: 50%;
            bottom: -10%;
            transform: translateX(50%);
            @include break-point(tablet) {
                width: 220px;
                left: 50%;
                bottom: -4%;
                transform: translateX(60%);
            }
            @include break-point(desktop) {
                width: rateValue(280px, 0.65);
                left: 50%;
                bottom: -7%;
                transform: translateX(94%);
            }
        }
        .dot-yellow {
            @apply absolute;
            width: vw(175px, 750px);
            left: -3%;
            top: vw(300px, 750px);
            transform: translate(0, 0);
            @include break-point(tablet) {
                width: 150px;
                left: 50%;
                top: 220px;
                transform: translate(-300%, 0);
            }
            @include break-point(desktop) {
                width: rateValue(200px, 0.65);
                left: 50%;
                top: rateValue(300px);
                transform: translate(-15%, 0);
            }
        }
        .arrow {
            @include aspect-ratio(2499, 3406);
            @include image2x(
                url(../../assets/images/graphics/arrow.png),
                url(../../assets/images/graphics/arrow@2x.png),
                cover
            );
            @apply absolute;
            width: vw(2135px, 750px);
            top: -8%;
            left: 50%;
            transform: translateX(-74.5%);
            @include break-point(tablet) {
                width: 1785px;
                top: -10%;
                left: 50%;
                transform: translateX(-80.5%);
            }
            @include break-point(desktop) {
                width: rateValue(2500px, 0.65);
                top: -24%;
                left: 50%;
                transform: translateX(-56%);
            }
        }
        .io {
            @apply absolute;
            transform-origin: center center;
            opacity: 0.2;
            &-1 {
                width: vw(845px, 750px);
                left: 50%;
                top: 50%;
                transform: translate(-53.2%, -600%) rotate(-60deg);
                @include break-point(tablet) {
                    width: 800px;
                    left: 50%;
                    top: 50%;
                    transform: translate(-73.2%, -50%) rotate(-60deg);
                }
                @include break-point(desktop) {
                    width: rateValue(1200px, 0.65);
                    left: 50%;
                    top: 50%;
                    transform: translate(-16.5%, 475%) rotate(-60deg);
                }
            }
            &-2 {
                width: vw(845px, 750px);
                left: 50%;
                top: 4%;
                transform: translate(-42%, 0) rotate(-60deg);
                @include break-point(tablet) {
                    width: 750px;
                    left: 50%;
                    top: -2%;
                    transform: translate(-55%, 0) rotate(-60deg);
                }
                @include break-point(desktop) {
                    width: rateValue(1000px);
                    left: 50%;
                    top: -1%;
                    transform: translate(10%, 0) rotate(-60deg);
                }
            }
            &-3 {
                width: vw(600px, 750px);
                left: 50%;
                top: 50%;
                transform: translate(-23.5%, -180%) rotate(-60deg);
                opacity: 1;
                @include break-point(tablet) {
                    width: 500px;
                    left: 50%;
                    top: 50%;
                    transform: translate(-45.5%, -180%) rotate(-60deg);
                }
                @include break-point(desktop) {
                    width: rateValue(600px);
                    left: 50%;
                    top: 50%;
                    transform: translate(72.3%, 900%) rotate(-60deg);
                }
            }
            &-4 {
                display: none;
                @include break-point(desktop) {
                    @apply absolute;
                    display: block;
                    width: rateValue(700px);
                    left: 50%;
                    top: 50%;
                    transform: translate(-150%, -550%) rotate(-60deg);
                }
            }
            &-5 {
                display: none;
                @include break-point(desktop) {
                    @apply absolute;
                    display: block;
                    width: rateValue(1000px);
                    left: 50%;
                    top: 50%;
                    transform: translate(-140%, 238%) rotate(-60deg);
                }
            }
        }
        .page-title-group {
            @apply absolute;
            transform-origin: center center;
            left: 50%;
            top: 50%;
            transform: translate(-61.5%, -200%) rotate(-60deg);
            @include break-point(tablet) {
                left: 50%;
                top: 50%;
                transform: translate(-82%, -202%) rotate(-60deg);
            }
            @include break-point(desktop) {
                left: 50%;
                top: 50%;
                transform: translate(-2.5%, -90%) rotate(-60deg);
            }
            .page-title {
                @apply whitespace-nowrap;
                font-size: vw(104px, 750px);
                line-height: 1.2;
                letter-spacing: vw(-2.6px, 750px);
                @include break-point(tablet) {
                    font-size: 86px;
                    line-height: 1.1;
                    letter-spacing: -2.15px;
                }
                @include break-point(desktop) {
                    line-height: 1.1;
                    font-size: rateValue(120px, 0.65);
                    letter-spacing: rateValue(-3px, 0.65);
                }
            }
            .page-caption {
                @apply whitespace-nowrap;
                font-size: vw(49px, 750px);
                line-height: 1;
                letter-spacing: vw(-1.23px, 750px);
                @include break-point(tablet) {
                    font-size: 40px;
                    letter-spacing: -1px;
                }
                @include break-point(desktop) {
                    font-size: rateValue(52px, 0.65);
                    letter-spacing: rateValue(-1.3px, 0.65);
                    transform: translateX(-30px);
                }
            }
        }
    }

    .intro-section {
        @apply bg-gray-light;
        padding-top: vw(200px, 750px);
        padding-bottom: 5vw;
        @include break-point(tablet) {
            padding-top: 140px;
            padding-bottom: 140px;
        }
        @include break-point(desktop) {
            padding-top: rateValue(260px);
            padding-bottom: rateValue(260px);
        }

        .container {
            @apply relative;
            z-index: 2;
        }
    }

    .career-section {
        @apply relative;
        padding-bottom: 10vw;
        z-index: 2;
        @include break-point(tablet) {
            padding-bottom: 80px;
        }
        @include break-point(desktop) {
            padding-bottom: rateValue(80px);
        }
        .section-cover {
            @include aspect-ratio(1500, 1284);
            @include image2x(
                url(../../assets/images/family/xs/cover.jpg),
                url(../../assets/images/family/xs/cover@2x.jpg),
                cover
            );
            @include break-point(tablet) {
                @include aspect-ratio(1199, 800);
                @include image2x(
                    url(../../assets/images/family/sm/cover.jpg),
                    url(../../assets/images/family/sm/cover@2x.jpg),
                    cover
                );
            }
            @include break-point(desktop) {
                @include aspect-ratio(1920, 1200);
                @include image2x(
                    url(../../assets/images/family/md/cover.jpg),
                    url(../../assets/images/family/md/cover@2x.jpg),
                    cover
                );
            }
            &-outer {
                @apply relative;
                @apply w-full;
                left: 50%;
                transform: translateX(-50%);
                @include break-point(tablet) {
                    width: 1200px;
                }
                @include break-point(desktop) {
                    width: 1920px;
                }
            }
        }
        .dot {
            &-1 {
                @apply absolute;
                width: vw(460px, 750px);
                left: 50%;
                bottom: vw(-230px, 750px);
                transform: translateX(-50%);
                @include break-point(tablet) {
                    width: 360px;
                    left: 50%;
                    bottom: -70px;
                    transform: translateX(-70%);
                }
                @include break-point(desktop) {
                    width: rateValue(560px);
                    left: 50%;
                    bottom: rateValue(-230px);
                    transform: translateX(-50%);
                }
            }
            &-2 {
                @apply absolute;
                width: vw(215px, 750px);
                left: 50%;
                bottom: -135.6%;
                transform: translateX(7%);
                @include break-point(tablet) {
                    width: 168px;
                    left: 50%;
                    bottom: -235px;
                    transform: translateX(-54%);
                }
                @include break-point(desktop) {
                    width: rateValue(425px);
                    left: auto;
                    right: rateValue(580px);
                    bottom: rateValue(-100px);
                    transform: translateX(0);
                }
            }
            &-3 {
                @apply absolute;
                width: vw(305px, 750px);
                left: 50%;
                bottom: -79%;
                transform: translateX(-102%);
                @include break-point(tablet) {
                    width: 240px;
                    left: 50%;
                    bottom: -150px;
                    transform: translateX(-195%);
                }
                @include break-point(desktop) {
                    width: rateValue(480px);
                    left: rateValue(520px);
                    bottom: rateValue(100px);
                    transform: translateX(0);
                }
            }
            &-4 {
                @apply absolute;
                width: vw(256px, 750px);
                left: 50%;
                bottom: -118%;
                transform: translateX(-81%);
                @include break-point(tablet) {
                    width: 200px;
                    left: 50%;
                    bottom: -300px;
                    transform: translateX(-147%);
                }
                @include break-point(desktop) {
                    width: rateValue(430px);
                    left: rateValue(-100px);
                    bottom: rateValue(-215px);
                    transform: translateX(0);
                }
            }
            &-5 {
                @apply absolute;
                width: vw(500px, 750px);
                left: 50%;
                bottom: -104.3%;
                transform: translateX(-1.5%);
                @include break-point(tablet) {
                    width: 390px;
                    left: 50%;
                    bottom: -270px;
                    transform: translateX(18%);
                }
                @include break-point(desktop) {
                    width: rateValue(600px);
                    left: auto;
                    right: rateValue(-120px);
                    bottom: rateValue(70px);
                    transform: translateX(0);
                }
            }
        }

        .section-grid {
            margin: vw(-40px, 750px) -30px;
            @include break-point(tablet) {
                margin: -40px;
            }
            @include break-point(desktop) {
                margin: rateValue(-75px);
            }
            > div {
                padding: vw(40px, 750px) 30px;
                @include break-point(tablet) {
                    padding: 40px;
                }
                @include break-point(desktop) {
                    padding: rateValue(75px);
                }
                .section-info {
                    &:last-child {
                        margin-bottom: 0 !important;
                    }
                }
            }
        }
        .section-info {
            &-outter {
                @apply bg-white;
                padding-top: vw(1060px, 750px);
                @include break-point(tablet) {
                    padding-top: 400px;
                }
                @include break-point(desktop) {
                    padding-top: rateValue(320px);
                }
            }
            &-cover {
                @apply relative;
                width: calc(100% + 40px);
                left: -20px;
                @include break-point(tablet) {
                    width: 100%;
                    left: 0;
                }
                @include break-point(desktop) {
                    width: 100%;
                    left: 0;
                }
                > .flex {
                    margin: vw(-10px, 750px) 0;
                    @include break-point(tablet) {
                        margin: -7.5px 0;
                    }
                    @include break-point(desktop) {
                        margin: 10px 0;
                    }
                    > div {
                        padding: vw(10px, 750px) 0;
                        @include break-point(tablet) {
                            padding: 7.5px 0;
                        }
                        @include break-point(desktop) {
                            padding: 10px 0;
                        }
                    }
                }
            }
        }
        .social {
            &-list {
                padding: 0;
                list-style: none;
                margin: vw(-20px, 750px);
                @include break-point(tablet) {
                    margin: -10px;
                }
                @include break-point(desktop) {
                    margin: rateValue(-15px);
                }
                > li {
                    padding: vw(20px, 750px);
                    @include break-point(tablet) {
                        padding: 10px;
                    }
                    @include break-point(desktop) {
                        padding: rateValue(15px);
                    }
                }
            }
            &-link {
                @apply flex;
                @apply flex-col-reverse;
                @apply justify-center;
                @apply items-center;
                @apply bg-primary;
                @apply text-white;
                @apply text-center;
                @apply rounded;
                padding: vw(40px, 750px);
                font-size: vw(40px, 750px);
                font-weight: 400;
                line-height: 1.29;
                letter-spacing: normal;
                @include break-point(tablet) {
                    padding: 60px 40px;
                    font-size: 24px;
                    line-height: 1.42;
                }
                @include break-point(desktop) {
                    @apply flex-row;
                    @apply justify-between;
                    @apply items-center;
                    @apply bg-primary;
                    @apply text-white;
                    @apply text-left;
                    @apply rounded;
                    @apply h-full;
                    padding: rateValue(40px) rateValue(80px);
                    font-size: rateValue(40px);
                    line-height: 1.29;
                }
                &-text {
                    @include break-point(desktop) {
                        margin-right: rateValue(80px);
                    }
                }
                &-icon {
                    @apply flex;
                    @apply justify-center;
                    @apply items-center;
                    @apply bg-white;
                    @apply rounded-full;
                    width: vw(150px, 750px);
                    height: vw(150px, 750px);
                    margin-bottom: vw(20px, 750px);
                    @include break-point(tablet) {
                        margin-bottom: 30px;
                        width: 150px;
                        height: 150px;
                    }
                    @include break-point(desktop) {
                        flex: 0 0 rateValue(150px);
                        width: rateValue(150px);
                        height: rateValue(150px);
                        margin-bottom: 0px;
                    }

                    .icon-104 {
                        @apply block;
                        width: 80%;
                        height: auto;
                    }
                    .icon-fb {
                        @apply block;
                        width: 60%;
                        height: auto;
                    }
                }
            }
            &-link-mobile {
                display: flex;
                @include break-point(desktop) {
                    display: none;
                }
            }
            &-link-desktop {
                display: none;
                @include break-point(desktop) {
                    display: flex;
                }
            }
        }

        .carousel-outer {
            @apply relative;
            width: vw(1300px, 750px);
            left: -30px;
            @include break-point(tablet) {
                width: 1200px;
                left: 50%;
                transform: translateX(-50%);
            }
            @include break-point(desktop) {
                width: 1920px;
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }
}

:lang(en) {
    #familyPage {
        .page-head {
            .page-title-group {
                transform: translate(-61.5%, -200%) rotate(-60deg);
                @include break-point(tablet) {
                    transform: translate(-82.5%, -202%) rotate(-60deg);
                }
                @include break-point(desktop) {
                    transform: translate(-2.5%, -90%) rotate(-60deg);
                }
                .page-title {
                    line-height: 1.2;
                    @include break-point(desktop) {
                        line-height: 1.1;
                    }
                }
            }
        }
    }
}
:lang(zh-hant) {
    #familyPage {
        .page-head {
            .page-title-group {
                transform: translate(-61.5%, -220%) rotate(-60deg);
                @include break-point(tablet) {
                    transform: translate(-86%, -233%) rotate(-60deg);
                }
                @include break-point(desktop) {
                    transform: translate(8%, -90%) rotate(-60deg);
                }
                .page-title {
                    line-height: 1.4;
                }
            }
        }
    }
}
