@mixin break-point($media) {
    @if $media == tablet {
        @media only screen and (min-width: theme("screens.sm")) {
            @content;
        }
    } @else if $media == desktop {
        @media only screen and (min-width: theme("screens.md")) {
            @content;
        }
    } @else if $media == large-desktop {
        @media only screen and (min-width: theme("screens.xl")) {
            @content;
        }
    }
}

@mixin aspect-ratio($width, $height, $position: relative, $overflow: hidden) {
    position: $position;
    overflow: $overflow;
    &:before {
        display: block;
        content: "";
        width: 100%;
        padding-top: ($height / $width) * 100%;
    }
    > * {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }
}

@mixin image2x($fallback, $image-2x, $size: cover, $detect-retina: true) {
    background-image: $fallback;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: $size;
    @media only screen and (-webkit-min-device-pixel-ratio: 1.5),
        only screen and (min--moz-device-pixel-ratio: 1.5),
        only screen and (-o-min-device-pixel-ratio: 2/1),
        only screen and (min-device-pixel-ratio: 1.5),
        only screen and (min-resolution: 144dpi),
        only screen and (min-resolution: 1.5dppx) {
        background-image: $image-2x;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: $size;
    }
}
