@import "../utils/extends.scss";
@import "../utils/functions.scss";

#growthPage {
    .page-head {
        @apply relative;
        @apply w-full;
        @apply bg-gray-light;
        @include break-point(tablet) {
        }
        @include break-point(desktop) {
        }

        &::after {
            @apply block;
            @apply absolute;
            @apply bg-primary;
            content: "";
            width: vw(950px, 750px);
            height: vw(4300px, 750px);
            left: -70%;
            top: 0;
            transform: translate(0, 0) skew(-30deg);
            transform-origin: center center;
            z-index: 1;
            @include break-point(tablet) {
                width: 700px;
                height: 2630px;
                top: 0;
                left: 50%;
                transform: translate(-85%, 0) skew(-30deg);
            }
            @include break-point(desktop) {
                width: 830px;
                transform: translateX(-60%) skew(-30deg);
            }
        }
        .kv {
            @include aspect-ratio(750, 1200);
            @include image2x(
                url(../../assets/images/growth/xs/kv.jpg),
                url(../../assets/images/growth/xs/kv@2x.jpg),
                cover
            );
            @apply relative;
            width: calc(100% + 40px);
            left: 50%;
            transform: translateX(-50%);
            margin-bottom: vw(145px, 750px);
            @include break-point(tablet) {
                @include aspect-ratio(1200, 980);
                @include image2x(
                    url(../../assets/images/growth/sm/kv.jpg),
                    url(../../assets/images/growth/sm/kv@2x.jpg),
                    cover
                );
                width: 1200px;
                margin-bottom: rateValue(375px);
            }
            @include break-point(desktop) {
                @include aspect-ratio(1920, 1024);
                @include image2x(
                    url(../../assets/images/growth/md/kv.jpg),
                    url(../../assets/images/growth/md/kv@2x.jpg),
                    cover
                );
                width: 1920px;
                margin-bottom: rateValue(375px);
            }
        }
        .page-title-group {
            @apply absolute;
            left: 3%;
            top: 36%;
            transform: translate(0, 0) rotate(-60deg);
            z-index: 3;
            @include break-point(tablet) {
                left: 50%;
                top: 50%;
                transform: translate(-49%, -90%) rotate(-60deg);
            }
            @include break-point(desktop) {
                transform: translate(-18%, -225%) rotate(-60deg);
            }
            .page-title {
                @apply whitespace-nowrap;
                @apply text-right;
                font-size: vw(90px, 750px);
                line-height: 1.18;
                letter-spacing: -2.25px;
                @include break-point(tablet) {
                    font-size: 76px;
                }
                @include break-point(desktop) {
                    @apply text-center;
                    font-size: rateValue(90px, 0.65);
                }
            }
            .page-caption {
                @apply whitespace-nowrap;
                @apply text-right;
                font-size: vw(40px, 750px);
                line-height: 1.45;
                letter-spacing: -1px;
                @include break-point(tablet) {
                    font-size: 34px;
                }
                @include break-point(desktop) {
                    @apply text-center;
                    font-size: rateValue(40px, 0.65);
                }
            }
        }

        .page-desc {
            @apply relative;
            @apply bg-white;
            width: calc(100% + 40px);
            left: -20px;
            padding: vw(60px, 750px) 20px;
            margin-bottom: vw(230px, 750px);
            z-index: 3;
            @include break-point(tablet) {
                @apply w-full;
                @apply bg-transparent;
                left: 0;
                padding: 0;
                margin-bottom: 190px;
            }
            @include break-point(desktop) {
                margin-bottom: rateValue(350px, 0.65);
                z-index: 3;
            }
            &-dot {
                @apply absolute;
                @apply border-none;
                display: none;
                width: vw(320px, 750px);
                left: vw(-160px, 750px);
                top: vw(-160px, 750px);
                z-index: 3;
                @include break-point(tablet) {
                    display: block;
                    width: 260px;
                    left: -130px;
                    top: -130px;
                }
                @include break-point(desktop) {
                    width: rateValue(240px);
                    left: rateValue(-120px);
                    top: rateValue(-120px);
                }
            }
        }
        .arrow {
            @include aspect-ratio(2499, 3406);
            @include image2x(
                url(../../assets/images/graphics/arrow.png),
                url(../../assets/images/graphics/arrow@2x.png),
                cover
            );
            @apply absolute;
            width: vw(1900px, 750px);
            top: vw(440px, 750px);
            left: 50%;
            transform: translate(-75%, 0);
            z-index: 2;
            @include break-point(tablet) {
                width: 1592px;
                top: 300px;
                left: 50%;
                transform: translate(-74.5%, 0);
            }
            @include break-point(desktop) {
                width: rateValue(2500px, 0.65);
                top: rateValue(350px);
                left: 50%;
                transform: translateX(-63.2%);
            }

            .dot-black {
                @apply absolute;
                width: vw(210px, 750px);
                right: 7%;
                top: 2%;
                @include break-point(tablet) {
                    width: 180px;
                    right: 7%;
                    top: 2%;
                }
                @include break-point(desktop) {
                    width: rateValue(280px, 0.65);
                    right: 10%;
                    top: 1%;
                }
            }
            .io {
                @apply absolute;
                width: vw(460px, 750px);
                left: 63.5%;
                top: 21%;
                transform: translate(0, 0) rotate(-60deg);
                z-index: 2;
                @include break-point(tablet) {
                    width: 460px;
                    left: 50%;
                    top: 22.5%;
                    transform: translate(34%, 0) rotate(-60deg);
                }
                @include break-point(desktop) {
                    width: 460px;
                    left: 50%;
                    top: 25.5%;
                    transform: translate(-55%, 0) rotate(-60deg);
                }
            }
        }

        .model {
            @include aspect-ratio(1736, 1788);
            @include image2x(
                url(../../assets/images/growth/model.png),
                url(../../assets/images/growth/model@2x.png),
                cover
            );
            @apply w-full;
            @include break-point(tablet) {
            }
            @include break-point(desktop) {
            }
            &-group {
                @apply relative;
                width: 115vw;
                left: -20%;
                z-index: 2;
                @include break-point(tablet) {
                    width: 760px;
                    left: 50%;
                    transform: translateX(-54%);
                }
                @include break-point(desktop) {
                    width: rateValue(900px, 0.65);
                    left: 50%;
                    transform: translate(-38%);
                }

                .text-agile {
                    @apply absolute;
                    @apply text-black;
                    font-size: vw(180px, 750px);
                    font-weight: 600;
                    line-height: 1.18;
                    letter-spacing: -4.58px;
                    left: 50%;
                    top: 50%;
                    transform: translate(-75%, -255%) rotate(-60deg);
                    @include break-point(tablet) {
                        font-size: 128px;
                        transform: translate(-89%, -305%) rotate(-60deg);
                    }
                    @include break-point(desktop) {
                        font-size: rateValue(200px, 0.65);
                        transform: translate(35%, -250%) rotate(-60deg);
                    }
                }
                .text-mangement {
                    @apply absolute;
                    @apply text-white;
                    font-size: vw(120px, 750px);
                    font-weight: 600;
                    line-height: 1.18;
                    letter-spacing: -3.08px;
                    left: 50%;
                    top: 50%;
                    transform: translate(-14%, -52%) rotate(-60deg);
                    @include break-point(tablet) {
                        font-size: 108px;
                        transform: translate(-14%, -76%) rotate(-60deg);
                    }
                    @include break-point(desktop) {
                        font-size: rateValue(170px, 0.65);
                        transform: translate(-5%, -170%) rotate(-60deg);
                    }
                }
            }
        }
        .pattern {
            @include aspect-ratio(1500, 1192, relative, visible);
            @include image2x(
                url(../../assets/images/growth/xs/pattern.png),
                url(../../assets/images/growth/xs/pattern@2x.png),
                cover
            );
            @apply relative;
            @apply w-full;
            margin-top: -56%;
            z-index: 5;
            @include break-point(tablet) {
                @include aspect-ratio(2400, 1165, relative, visible);
                @include image2x(
                    url(../../assets/images/growth/sm/pattern.png),
                    url(../../assets/images/growth/sm/pattern@2x.png),
                    cover
                );
                width: 1200px;
                left: 50%;
                transform: translate(-50%, 0);
                margin-top: -440px;
            }
            @include break-point(desktop) {
                @include aspect-ratio(1920, 482, relative, visible);
                @include image2x(
                    url(../../assets/images/growth/md/pattern.png),
                    url(../../assets/images/growth/md/pattern@2x.png),
                    cover
                );
                width: 1920px;
                margin-top: -380px;
                padding-bottom: 100px;
            }

            &::after {
                @include break-point(desktop) {
                    @apply absolute;
                    @apply block;
                    @apply w-full;
                    @apply bg-white;
                    height: 50px;
                    content: "";
                    left: -100px;
                    bottom: 0;
                }
            }

            &-img {
                @apply relative;
                z-index: 1;
                @include break-point(tablet) {
                }
                @include break-point(desktop) {
                }
            }

            .dot {
                &-1 {
                    @apply absolute;
                    width: vw(460px, 750px);
                    left: 50%;
                    top: 141%;
                    transform: translate(-82%, 0);
                    z-index: 1;
                    @include break-point(tablet) {
                        width: 405px;
                        left: 50%;
                        top: 146%;
                        transform: translate(-81%, 0);
                        z-index: 1;
                    }
                    @include break-point(desktop) {
                        width: rateValue(460px, 0.65);
                        left: 50%;
                        top: 50%;
                        transform: translate(-150%, -190%);
                    }
                }
                &-2 {
                    @apply absolute;
                    width: vw(260px, 750px);
                    left: 50%;
                    top: 98%;
                    transform: translate(-114%, 0);
                    z-index: 1;
                    @include break-point(tablet) {
                        width: 228px;
                        left: 50%;
                        top: 107%;
                        transform: translate(-114%, 0);
                    }
                    @include break-point(desktop) {
                        width: rateValue(285px, 0.65);
                        left: 50%;
                        top: 50%;
                        transform: translate(2%, 56%);
                    }
                }
                &-3 {
                    @apply absolute;
                    width: vw(300px, 750px);
                    left: 50%;
                    top: 59%;
                    transform: translate(-50%, 0);
                    z-index: 1;
                    @include break-point(tablet) {
                        width: 260px;
                        left: 50%;
                        top: 72%;
                        transform: translate(-50%, 0);
                    }
                    @include break-point(desktop) {
                        width: rateValue(320px, 0.65);
                        left: 50%;
                        top: 50%;
                        transform: translate(-166%, -70%);
                    }
                }
                &-4 {
                    @apply absolute;
                    width: vw(340px, 750px);
                    left: 50%;
                    top: 63.5%;
                    transform: translate(43%, 0);
                    z-index: 1;
                    @include break-point(tablet) {
                        width: 297px;
                        left: 50%;
                        top: 74.5%;
                        transform: translate(43%, 0);
                    }
                    @include break-point(desktop) {
                        width: rateValue(400px, 0.65);
                        left: -2%;
                        top: 50%;
                        transform: translate(0, 6%);
                    }
                }
                &-5 {
                    @apply absolute;
                    width: vw(300px, 750px);
                    left: 50%;
                    top: 106.3%;
                    transform: translate(-15%, 0);
                    z-index: 1;
                    @include break-point(tablet) {
                        width: 267px;
                        left: 50%;
                        top: 114%;
                        transform: translate(-15%, 0);
                    }
                    @include break-point(desktop) {
                        width: rateValue(500px, 0.65);
                        left: 50%;
                        top: 50%;
                        transform: translate(85%, -54%);
                    }
                }
            }
        }
    }

    .intro-section {
        @apply relative;
        @apply bg-white;
        z-index: 4;
        padding-top: vw(850px, 750px);
        padding-bottom: vw(80px, 750px);
        @include break-point(tablet) {
            padding-top: 830px;
            padding-bottom: rateValue(120px);
        }
        @include break-point(desktop) {
            padding-top: rateValue(100px);
            padding-bottom: rateValue(120px);
        }

        > .container {
            .section-info {
                &:last-child {
                    margin-bottom: 0 !important;
                }
            }
        }

        .section-info {
            margin-bottom: vw(200px, 750px);
            @include break-point(tablet) {
                margin-bottom: 200px;
            }
            @include break-point(desktop) {
                margin-bottom: rateValue(120px, 0.65);
            }
            &-title {
                @apply relative;
                z-index: 2;
                @include break-point(tablet) {
                }
                @include break-point(desktop) {
                }
            }
            &-desc {
                @apply relative;
                z-index: 2;
                @include break-point(tablet) {
                }
                @include break-point(desktop) {
                }
            }
            &-list {
                > li {
                    margin-bottom: 0;
                    @include break-point(tablet) {
                    }
                    @include break-point(desktop) {
                    }
                }
            }
            &-cover {
                @apply relative;
                z-index: 2;
                @include break-point(tablet) {
                }
                @include break-point(desktop) {
                }

                &.process {
                    @include aspect-ratio(1014, 800);
                    @include image2x(
                        url(../../assets/images/growth/process.png),
                        url(../../assets/images/growth/process@2x.png),
                        contain
                    );
                    @apply w-full;
                    max-width: rateValue(1010px);
                    @include break-point(tablet) {
                        max-width: rateValue(1010px);
                    }
                    @include break-point(desktop) {
                        width: rateValue(1010px, 0.65);
                    }
                }
                &.technology {
                    @include aspect-ratio(646, 882);
                    @include image2x(
                        url(../../assets/images/growth/xs/technology.png),
                        url(../../assets/images/growth/xs/technology@2x.png),
                        contain
                    );
                    width: 100%;
                    /*  left: 50%;
                    transform: translateX(-56%); */
                    @include break-point(tablet) {
                        @include aspect-ratio(1088, 823);
                        @include image2x(
                            url(../../assets/images/growth/technology.png),
                            url(../../assets/images/growth/technology@2x.png),
                            contain
                        );
                        width: 100%;
                        max-width: 1080px;
                        left: 0;
                        transform: translateX(0);
                    }

                    @include break-point(desktop) {
                        width: rateValue(1080px, 0.65);
                    }
                }
                &.ux {
                    @include aspect-ratio(933, 932);
                    @include image2x(
                        url(../../assets/images/growth/ux.png),
                        url(../../assets/images/growth/ux@2x.png),
                        contain
                    );
                    @apply w-full;
                    max-width: rateValue(930px);
                    @include break-point(tablet) {
                        max-width: auto;
                    }
                    @include break-point(desktop) {
                        width: rateValue(930px, 0.65);
                    }
                }
            }
            &-ux {
                @apply relative;
                @include break-point(tablet) {
                }
                @include break-point(desktop) {
                }
                &::before {
                    @apply block;
                    @apply absolute;
                    @apply bg-primary;
                    content: "";
                    width: vw(1000px, 750px);
                    height: vw(3500px, 750px);
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -62%) skew(-30deg);
                    @include break-point(tablet) {
                        width: 100vw;
                        height: 2400px;
                        left: 50%;
                        top: auto;
                        bottom: -12%;
                        transform: translate(0, 0) skew(-30deg);
                    }
                    @include break-point(desktop) {
                        content: none;
                    }
                }
            }
            &-list-group {
                padding: vw(40px, 750px);
                @include break-point(tablet) {
                    padding: 30px;
                }
                @include break-point(desktop) {
                }
            }
        }
        .cover-group {
            @apply relative;
            @include break-point(tablet) {
            }
            @include break-point(desktop) {
            }

            .arrow-group {
                @apply absolute;
                left: -20px;
                top: -128%;
                @include break-point(tablet) {
                    left: -70px;
                    top: 50%;
                    transform: translateY(-83%);
                }
                @include break-point(desktop) {
                    left: 50%;
                    top: 50%;
                    transform: translate(-60%, -55%);
                }

                &::before {
                    @include break-point(desktop) {
                        @apply block;
                        @apply absolute;
                        @apply bg-primary;
                        content: "";
                        width: 2000px;
                        height: 2000px;
                        left: 89.2%;
                        top: -8%;
                        transform: rotate(-60deg);
                        z-index: 1;
                    }
                }
                &::after {
                    @include break-point(desktop) {
                        @apply block;
                        @apply absolute;
                        @apply bg-primary;
                        content: "";
                        width: 2000px;
                        height: 2000px;
                        left: -112%;
                        bottom: -32%;
                        transform: rotate(120deg);
                        z-index: 1;
                    }
                }
            }
            .arrow {
                @include aspect-ratio(1500, 3330);
                @include image2x(
                    url(../../assets/images/growth/xs/arrow-gray.png),
                    url(../../assets/images/growth/xs/arrow-gray@2x.png),
                    cover
                );
                width: 115vw;
                @include break-point(tablet) {
                    @include aspect-ratio(1860, 2784);
                    @include image2x(
                        url(../../assets/images/growth/sm/arrow-gray.png),
                        url(../../assets/images/growth/sm/arrow-gray@2x.png),
                        cover
                    );
                    width: 930px;
                }
                @include break-point(desktop) {
                    @include aspect-ratio(2142, 2919);
                    @include image2x(
                        url(../../assets/images/growth/md/arrow-gray.png),
                        url(../../assets/images/growth/md/arrow-gray@2x.png),
                        cover
                    );
                    width: rateValue(2140px, 0.65);
                    z-index: 2;
                }
            }
        }
    }
}
:lang(en) {
    #growthPage {
        .page-head {
            .page-title-group {
                @apply absolute;
                left: 3%;
                top: 36%;
                transform: translate(0, 0) rotate(-60deg);
                z-index: 3;
                @include break-point(tablet) {
                    left: 50%;
                    top: 50%;
                    transform: translate(-49%, -90%) rotate(-60deg);
                }
                @include break-point(desktop) {
                    transform: translate(-18%, -225%) rotate(-60deg);
                }
            }
        }
    }
}
:lang(zh-hant) {
    #growthPage {
        .page-head {
            .page-title-group {
                left: 21%;
                top: 36%;
                transform: translate(0, 0) rotate(-60deg);
                @include break-point(tablet) {
                    left: 50%;
                    top: 50%;
                    transform: translate(-22%, -200%) rotate(-60deg);
                }
                @include break-point(desktop) {
                    transform: translate(0%, -195%) rotate(-60deg);
                }
                .page-title {
                    line-height: 1.4;
                }
            }
        }
        .education-training {
            margin-top: 50vw;
            @include break-point(tablet) {
                margin-top: 0;
            }
        }
    }
}
