@import "../utils/mixins";

.contact-section {
    @apply w-full;
    @apply bg-primary;
    padding-top: vw(60px, 750px);
    padding-bottom: vw(60px, 750px);
    z-index: 4;
    @include break-point(tablet) {
    }
    @include break-point(desktop) {
        padding-top: rateValue(80px);
        padding-bottom: rateValue(80px);
    }

    * {
        @apply text-white;
    }
    .address {
        margin: vw(-20px, 750px);

        @include break-point(tablet) {
            margin: vw(-5px, 750px);
        }
        @include break-point(desktop) {
            margin: rateValue(-10px);
        }

        > div {
            padding: vw(20px, 750px);
            @include break-point(tablet) {
                padding: vw(5px, 750px);
            }
            @include break-point(desktop) {
                padding: rateValue(10px);
            }
        }

        .address-info {
            &-label {
                @apply block;
                // font-weight: 600;
                margin-bottom: vw(5px, 750px);
                @include break-point(tablet) {
                    @apply inline-block;
                    margin-bottom: 0;
                }
                @include break-point(desktop) {
                }
            }
            a {
                display: block;
                text-decoration: underline;
                // font-weight: 600;
                @include break-point(tablet) {
                    @apply inline-block;
                    text-decoration: none;
                }
                @include break-point(desktop) {
                }

                &:hover {
                    color: theme("colors.white");
                    text-decoration: underline;
                }
            }
        }
    }
    .section-title {
        margin-bottom: vw(40px, 750px);
        @include break-point(tablet) {
            margin-bottom: 30px;
        }
        @include break-point(desktop) {
            margin-bottom: rateValue(40px);
        }
    }
}
