@import "../utils/functions";
@import "../utils/mixins";

#indexPage {
    .dot-yellow {
        width: 300px;
    }
    .page-head {
        @apply relative;
        @apply w-full;
        @apply bg-primary;
        height: vw(1652px, 750px);
        z-index: 1;
        @include break-point(tablet) {
            height: 1285px;
        }
        @include break-point(desktop) {
            max-height: 750px;
            min-height: 650px;
        }
        //
        &-inner {
            @apply relative;
            @apply w-full;
        }
        //
        .page-title-group {
            @apply relative;
            padding-top: vw(300px, 750px);
            @include break-point(tablet) {
                max-width: 768px;
                margin: 0 auto;
                padding-top: 280px;
            }
            @include break-point(desktop) {
                width: rateValue(680px, 0.7);
                max-width: none;
                margin: 0;
                top: 50%;
                transform: translateY(-50%);
                padding-top: 0;
            }

            //
            .page-title {
                @apply text-white;
                @apply w-full;
                @apply whitespace-nowrap;
                @apply font-semibold;
                margin-bottom: 20px;
                line-height: 0.75;
                @include break-point(tablet) {
                    line-height: 0.8;
                }
                @include break-point(desktop) {
                    line-height: 0.75;
                    margin-bottom: 10px;
                }
                span {
                    @apply inline-block;
                    @apply w-full;
                    font-size: vw(165px, 750px);
                    letter-spacing: vw(-8px, 750px);

                    &:last-child {
                        @apply text-right;
                        @include break-point(tablet) {
                            @apply text-left;
                            text-indent: 28%;
                        }
                        @include break-point(desktop) {
                            text-indent: 37%;
                        }
                    }
                    @include break-point(tablet) {
                        font-size: 114px;
                        letter-spacing: -5.7px;
                    }
                    @include break-point(desktop) {
                        font-size: 158px;
                        letter-spacing: -7.9px;
                    }
                }
            }
            .page-caption {
                @apply w-full;
                @apply font-medium;
                font-size: 5vw;
                line-height: 1.2;
                letter-spacing: normal;
                @include break-point(tablet) {
                    max-width: 420px;
                    font-size: 20.5px;
                }
                @include break-point(desktop) {
                    max-width: none;
                    font-size: rateValue(38px, 0.7);
                    line-height: 1;
                    letter-spacing: rateValue(-1.88px);
                }
            }
        }
        //
        .bg {
            @apply absolute;
            width: calc(100% + 40px);
            top: vw(676px, 750px);
            left: -20px;
            @include break-point(tablet) {
                width: 860px;
                top: 513px;
                left: 50%;
                transform: translateX(-68%);
            }
            @include break-point(desktop) {
                width: rateValue(2500px, 0.65);
                top: rateValue(80px);
                left: 50%;
                transform: translateX(-68%);
            }
            &-arrow {
                @include aspect-ratio(750, 1860);
                @apply w-full;
                @include image2x(
                    url(../../assets/images/index/xs/arrow.png),
                    url(../../assets/images/index/xs/arrow@2x.png)
                );
                @include break-point(tablet) {
                    @include aspect-ratio(857, 1414);

                    @include image2x(
                        url(../../assets/images/index/sm/arrow.png),
                        url(../../assets/images/index/sm/arrow@2x.png)
                    );
                }
                @include break-point(desktop) {
                    @include aspect-ratio(2499, 3406);

                    @include image2x(
                        url(../../assets/images/graphics/arrow.png),
                        url(../../assets/images/graphics/arrow@2x.png)
                    );
                }
            }
            &-dot {
                &-1 {
                    @apply absolute;
                    width: vw(190px, 750px);
                    left: 17%;
                    top: 9%;
                    @include break-point(tablet) {
                        width: 165px;
                        left: 46%;
                        top: 6%;
                    }
                    @include break-point(desktop) {
                        width: rateValue(165px, 0.65);
                        left: 83%;
                        top: 5%;
                    }
                }
                &-2 {
                    @apply absolute;
                    width: vw(160px, 750px);
                    left: 9%;
                    top: 32%;
                    @include break-point(tablet) {
                        width: 137px;
                        left: 38%;
                        top: 32%;
                    }
                    @include break-point(desktop) {
                        width: rateValue(160px, 0.65);
                        left: 76%;
                        top: 18%;
                    }
                }
                &-3 {
                    @apply absolute;
                    width: vw(210px, 750px);
                    right: -3.5%;
                    top: 33%;
                    @include break-point(tablet) {
                        width: 180px;
                        right: -15.5%;
                        top: 29%;
                    }
                    @include break-point(desktop) {
                        width: rateValue(280px, 0.65);
                        right: -4%;
                        top: 17%;
                    }
                }
                &-4 {
                    @apply absolute;
                    width: vw(95px, 750px);
                    right: 26%;
                    top: 23.5%;
                    @include break-point(tablet) {
                        width: 82px;
                        right: 11%;
                        top: 22.5%;
                    }
                    @include break-point(desktop) {
                        width: rateValue(125px, 0.65);
                        right: 4.5%;
                        top: 13%;
                    }
                }
                &-5 {
                    @apply absolute;
                    width: vw(215px, 750px);
                    left: -11%;
                    top: 50%;
                    @include break-point(tablet) {
                        width: 184px;
                        left: 22%;
                        top: 50%;
                    }
                    @include break-point(desktop) {
                        width: rateValue(325px, 0.65);
                        left: 38%;
                        top: 34%;
                    }
                }
            }
            &-io {
                &-1 {
                    @apply absolute;
                    width: vw(800px, 750px);
                    left: -51%;
                    top: 34%;
                    transform: rotate(-60deg);
                    @include break-point(tablet) {
                        width: 680px;
                        left: -1%;
                        top: 29%;
                    }
                    @include break-point(desktop) {
                        width: rateValue(1200px, 0.65);
                        left: 39%;
                        top: 32%;
                    }
                }
                &-2 {
                    @apply absolute;
                    width: vw(800px, 750px);
                    left: -64%;
                    top: 56%;
                    transform: rotate(-60deg);
                    @include break-point(tablet) {
                        width: 680px;
                        left: -13%;
                        top: 56%;
                    }
                    @include break-point(desktop) {
                        width: rateValue(1200px, 0.65);
                        left: 74%;
                        top: -3.8%;
                    }
                }
                &-3 {
                    @apply absolute;
                    width: vw(800px, 750px);
                    left: 2%;
                    top: 35%;
                    transform: rotate(-60deg);
                    @include break-point(tablet) {
                        width: 680px;
                        left: 36%;
                        top: 33%;
                        transform: rotate(-60deg);
                    }
                    @include break-point(desktop) {
                        width: rateValue(1300px, 0.65);
                        left: 58.3%;
                        top: 23%;
                    }
                }
                &-4 {
                    @apply absolute;
                    width: vw(1000px, 750px);
                    right: -88%;
                    top: 11%;
                    transform: rotate(-60deg);
                    @include break-point(tablet) {
                        width: 435px;
                        right: -43%;
                        top: 21%;
                    }
                    @include break-point(desktop) {
                        width: rateValue(700px, 0.65);
                        right: -19%;
                        top: 12%;
                    }
                }
                &-5 {
                    @apply absolute;
                    width: vw(1200px, 750px);
                    left: 50%;
                    top: 50%;
                    transform: translate(-67.5%, 1000%) rotate(-60deg);
                    transform-origin: center center;
                    opacity: 1;
                    @include break-point(tablet) {
                        width: 840px;
                        left: 4%;
                        top: 64%;
                        transform: rotate(-60deg);
                    }
                    @include break-point(desktop) {
                        @apply hidden;
                    }
                }
            }
        }
    }
    .intro-section {
        .bg {
            &-model {
                @apply relative;
                @apply w-full;
                @apply h-full;
                @include image2x(
                    url(../../assets/images/index/model.png),
                    url(../../assets/images/index/model@2x.png),
                    contain
                );
                &-group {
                    @include aspect-ratio(1116, 1540, relative, visible);
                    width: vw(1130px, 750px);
                    left: 50%;
                    transform: translateX(-56%);
                    margin-top: -70%;
                    z-index: 2;
                    @include break-point(tablet) {
                        width: 970px;
                        left: 50%;
                        transform: translateX(-56%);
                        margin-top: -400px;
                    }
                    @include break-point(desktop) {
                        width: rateValue(1116px);
                        left: 50%;
                        transform: translateX(-36%);
                        margin-top: 0;
                        padding-top: rateValue(110px);
                    }
                }
                &-dot {
                    &-1 {
                        @apply absolute;
                        width: vw(340px, 750px);
                        left: 14%;
                        bottom: 13%;
                        @include break-point(tablet) {
                            width: 295px;
                            left: 14%;
                            bottom: 13%;
                        }
                        @include break-point(desktop) {
                            width: rateValue(375px);
                            left: -12%;
                            bottom: 10%;
                        }
                    }
                    &-2 {
                        @apply absolute;
                        border: none;
                        width: vw(340px, 750px);
                        right: 13%;
                        top: 71%;
                        transform: translateY(-82%);
                        @include break-point(tablet) {
                            width: 295px;
                            right: 13%;
                            top: 71%;
                            transform: translateY(-82%);
                        }
                        @include break-point(desktop) {
                            width: rateValue(460px);
                            right: 0;
                            top: 50%;
                            transform: translateY(-82%);
                        }
                    }
                    &-3 {
                        @apply absolute;
                        @apply border-none;
                        width: vw(340px, 750px);
                        left: 25%;
                        top: 93%;
                        @include break-point(tablet) {
                            width: 340px;
                            left: 12%;
                            top: 120%;
                        }
                        @include break-point(desktop) {
                            display: none;
                        }
                    }
                    &-4 {
                        @apply absolute;
                        width: vw(150px, 750px);
                        right: 12%;
                        top: 111.5%;
                        @include break-point(tablet) {
                            width: 150px;
                            right: 10%;
                            top: 128.5%;
                        }
                        @include break-point(desktop) {
                            width: rateValue(200px);
                            right: -30px;
                            left: auto;
                            top: 50%;
                            transform: translateY(50%);
                        }
                    }
                }
                &-io {
                    @apply absolute;
                    display: none;
                    @include break-point(tablet) {
                        display: block;
                        width: rateValue(1260px);
                        right: -51%;
                        top: 50%;
                        transform: translateY(784%) rotate(-60deg);
                    }
                    @include break-point(desktop) {
                        width: rateValue(1260px);
                        right: -51%;
                        top: 50%;
                        transform: translateY(784%) rotate(-60deg);
                    }
                }
            }
            &-pattern {
                @apply relative;
                @apply w-full;
                @apply h-full;
                @include image2x(
                    url(../../assets/images/index/xs/pattern.png),
                    url(../../assets/images/index/xs/pattern@2x.png)
                );
                @include break-point(tablet) {
                    @include image2x(
                        url(../../assets/images/index/sm/pattern.png),
                        url(../../assets/images/index/sm/pattern@2x.png)
                    );
                }
                @include break-point(desktop) {
                    @include aspect-ratio(1920, 934);
                    @include image2x(
                        url(../../assets/images/index/md/pattern.png),
                        url(../../assets/images/index/md/pattern@2x.png)
                    );
                }
                &-front {
                    display: none;
                    @include break-point(desktop) {
                        @include aspect-ratio(1920, 934);
                        @apply absolute;
                        @include image2x(
                            url(../../assets/images/index/md/pattern-front.png),
                            url(../../assets/images/index/md/pattern-front@2x.png)
                        );
                        display: block;
                        width: 100%;
                        bottom: 0;
                        left: 0;
                    }
                }

                &-group {
                    @include aspect-ratio(750, 794, relative, visible);
                    @apply relative;
                    width: calc(100% + 60px);
                    left: -30px;
                    margin-top: vw(750px, 750px);
                    z-index: 1;
                    @include break-point(tablet) {
                        @include aspect-ratio(1200, 795, relative, visible);
                        width: 1200px;
                        left: 50%;
                        transform: translateX(-50%);
                        margin-top: 1100px;
                    }
                    @include break-point(desktop) {
                        @include aspect-ratio(1920, 934, relative, visible);
                        width: 1920px;
                        left: 50%;
                        transform: translateX(-50%);
                        margin-top: 0;
                        overflow: hidden;
                    }
                }

                &-io {
                    &-1 {
                        @apply absolute;
                        transform: rotate(-60deg);
                        width: vw(1220px, 750px);
                        top: -38%;
                        left: -35%;
                        @include break-point(tablet) {
                            width: 850px;
                            top: -45%;
                            left: 4%;
                        }
                        @include break-point(desktop) {
                            width: 860px;
                            top: auto;
                            bottom: 14%;
                            left: -2%;
                        }
                    }
                    &-2 {
                        @apply absolute;
                        transform: rotate(-60deg);
                        width: vw(900px, 750px);
                        top: -31%;
                        left: -25%;
                        @include break-point(tablet) {
                            width: 850px;
                            top: 12%;
                            left: -13%;
                        }
                        @include break-point(desktop) {
                            width: 1100px;
                            top: auto;
                            bottom: -8%;
                            left: -11%;
                        }
                    }
                }
            }
        }
        .link {
            @include aspect-ratio(1, 1, absolute, visible);
            @apply absolute;
            @apply rounded-full;
            &:hover {
                .link-icon {
                    transform: translateX(-50%) scale(1.1);
                    .icon {
                        transform: rotate(90deg);
                    }
                }
            }

            &-group {
                @apply absolute;
                @apply w-full;
                @apply h-full;
                top: 0;
                left: 0;
                z-index: 3;
                @include break-point(tablet) {
                }
                @include break-point(desktop) {
                }
            }
            &-info {
                @apply flex;
                @apply flex-col;
                justify-content: center;
                align-items: center;
                padding: 0 vw(20px, 750px);
                @include break-point(tablet) {
                    padding: 0 20px;
                }
                @include break-point(desktop) {
                    padding: 0 20px;
                }
                &-title {
                    @apply text-center;
                    font-weight: 500;
                    margin-bottom: vw(20px, 750px);
                    margin-top: vw(-40px, 750px);
                    @include break-point(tablet) {
                        margin-bottom: 20px;
                        margin-top: -40px;
                    }
                    @include break-point(desktop) {
                        @apply text-center;
                        margin-bottom: 20px;
                        margin-top: -40px;
                    }
                }
                &-desc {
                    @apply text-center;
                }
                h3 {
                    font-size: vw(70px, 750px);
                    line-height: 0.85;
                    letter-spacing: vw(-3.5px, 750px);
                    @include break-point(tablet) {
                        font-size: 55px;
                        line-height: 1;
                        letter-spacing: -2.75px;
                    }
                    @include break-point(desktop) {
                        font-size: rateValue(90px, 0.7);
                        line-height: 0.85;
                        letter-spacing: rateValue(-4.5px);
                    }
                }
                h4 {
                    font-size: vw(70px, 750px);
                    line-height: 0.85;
                    letter-spacing: vw(-3.5px, 750px);
                    @include break-point(tablet) {
                        font-size: 55px;
                        line-height: 1;
                        letter-spacing: -2.75px;
                    }
                    @include break-point(desktop) {
                        font-size: rateValue(71.5px);
                        line-height: 0.85;
                        letter-spacing: rateValue(-3.58px);
                    }
                }
                p {
                    font-size: vw(30px, 750px);
                    line-height: 1.33;
                    letter-spacing: normal;
                    @include break-point(tablet) {
                        font-size: 20px;
                        line-height: 1.4;
                        letter-spacing: normal;
                    }
                    @include break-point(desktop) {
                        font-size: 15px;
                        line-height: 1.5;
                        letter-spacing: normal;
                        font-weight: 500;
                    }
                }
            }
            &-icon {
                @apply flex;
                @apply justify-center;
                @apply items-center;
                @apply rounded-full;
                @apply bg-white;
                @apply absolute;
                bottom: vw(70px, 750px);
                left: 50%;
                transform: translateX(-50%);
                width: vw(60px, 750px);
                height: vw(60px, 750px);
                box-shadow: 0 0 21.8px 2.2px rgba(0, 0, 0, 0.16);
                transition: transform 0.3s ease-out;
                transform-origin: center center;
                @include break-point(tablet) {
                    bottom: 70px;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 50px;
                    height: 50px;
                    box-shadow: 0 0 21.8px 2.2px rgba(0, 0, 0, 0.16);
                }
                @include break-point(desktop) {
                    bottom: 70px;
                    left: 50%;
                    transform: translateX(-50%);
                    width: rateValue(66px);
                    height: rateValue(66px);
                    box-shadow: 0 0 21.8px 2.2px rgba(0, 0, 0, 0.16);
                }
                .icon {
                    width: vw(24px, 750px);
                    transition: transform 0.3s ease-out;
                    @include break-point(tablet) {
                        width: 20px;
                    }
                    @include break-point(desktop) {
                        width: rateValue(24px);
                    }
                }
            }
            &-about {
                @apply bg-yellow-400;
                width: vw(487px, 750px);
                top: 32%;
                left: 50%;
                transform: translateX(-24%);
                @include break-point(tablet) {
                    width: 440px;
                    top: 29%;
                    left: 50%;
                    transform: translateX(-14%);
                }
                @include break-point(desktop) {
                    width: rateValue(650px);
                    top: 18%;
                    left: 50%;
                    transform: translateX(-62%);
                }
            }
            &-growth {
                @apply bg-black;
                width: vw(505px, 750px);
                left: 0;
                top: 50%;
                transform: translateY(17%);
                @include break-point(tablet) {
                    @apply bg-black;
                    width: 505px;
                    left: 50%;
                    top: 49%;
                    transform: translateX(-66%);
                }
                @include break-point(desktop) {
                    width: rateValue(460px);
                    left: 50%;
                    top: 53%;
                    transform: translateX(5%);
                }

                .link-info-title {
                    margin-top: vw(-60px, 750px);
                    @include break-point(tablet) {
                        margin-top: -60px;
                    }
                    @include break-point(desktop) {
                        margin-top: rateValue(-40px);
                    }
                }
                .link-icon {
                    @include break-point(desktop) {
                        bottom: rateValue(-33px);
                    }
                }
            }
            &-family {
                @apply bg-white;
                width: 79vw;
                border: solid 2px #bababa;
                bottom: 16%;
                left: 50%;
                transform: translateX(-36%);
                @include break-point(tablet) {
                    width: 485px;
                    border: solid 4px #bababa;
                    bottom: 13%;
                    left: 50%;
                    transform: translateX(-50%);
                }
                @include break-point(desktop) {
                    width: rateValue(760px);
                    border: solid rateValue(4px) #bababa;
                    bottom: 25%;
                    left: 50%;
                    transform: translateX(-86%);
                }
            }
            &-service {
                @apply bg-primary;
                width: vw(466px, 750px);
                bottom: -4%;
                left: 50%;
                transform: translateX(-33%);
                @include break-point(tablet) {
                    width: 430px;
                    bottom: -5%;
                    left: 50%;
                    transform: translateX(-31%);
                }
                @include break-point(desktop) {
                    width: 450px;
                    bottom: -1%;
                    left: 50%;
                    transform: translateX(-23%);
                }
            }

            &-dot {
                &-1 {
                    @apply absolute;
                    width: vw(178px, 750px);
                    top: 60.9%;
                    left: 50%;
                    transform: translate(72%, 0);
                    @include break-point(tablet) {
                        width: 178px;
                        top: 61.4%;
                        left: 50%;
                        transform: translate(98%, 0);
                    }
                    @include break-point(desktop) {
                        width: rateValue(295px);
                        top: 74.4%;
                        left: 50%;
                        transform: translate(210%, 0);
                    }
                }
                &-2 {
                    @apply absolute;
                    display: none;
                    @include break-point(tablet) {
                    }
                    @include break-point(desktop) {
                        display: block;
                        width: rateValue(220px);
                        top: 81.4%;
                        left: 50%;
                        transform: translate(-240%, 0);
                    }
                }
                &-3 {
                    @apply absolute;
                    width: vw(360px, 750px);
                    bottom: 4%;
                    left: 50%;
                    transform: translate(-123%, 0);
                    @include break-point(tablet) {
                        width: 360px;
                        bottom: 5%;
                        left: 50%;
                        transform: translate(-123%, 0);
                    }
                    @include break-point(desktop) {
                        width: rateValue(460px);
                        bottom: -5%;
                        left: 50%;
                        transform: translate(-95%, 0);
                    }
                }
            }
            &-io {
                display: none;
                @include break-point(desktop) {
                    @apply absolute;
                    display: block;
                    width: rateValue(660px);
                    bottom: 3%;
                    left: 50%;
                    transform: translate(-148%, 0) rotate(-60deg);
                }
            }
        }

        .section-cover {
            @include aspect-ratio(750, 927);
            width: 100%;
            @include image2x(
                url(../../assets/images/index/xs/cover.jpg),
                url(../../assets/images/index/xs/cover@2x.jpg)
            );
            @include break-point(tablet) {
                @include aspect-ratio(1196, 857);
                @include image2x(
                    url(../../assets/images/index/sm/cover.jpg),
                    url(../../assets/images/index/sm/cover@2x.jpg)
                );
                background-position: top center !important;
                max-height: 600px;
                width: 1200px;
                left: 50%;
                transform: translateX(-50%);
            }
            @include break-point(desktop) {
                @include aspect-ratio(1920, 1114);

                @include image2x(
                    url(../../assets/images/index/md/cover.jpg),
                    url(../../assets/images/index/md/cover@2x.jpg)
                );
                background-position: top center !important;
                width: 1920px;
                max-height: rateValue(1600px);
                margin-top: -5px;
                z-index: 1;
            }
        }
    }

    .address-info-email {
        @include break-point(desktop) {
            transform: translateY(2px);
        }
    }
}
:lang(zh-hant) {
    #indexPage {
        //
        .page-head {
            .page-title-group {
                .page-title {
                    line-height: 0.95;
                    @include break-point(tablet) {
                    }
                    @include break-point(desktop) {
                        margin-bottom: 20px;
                    }
                    span {
                        font-size: 20vw;
                        @include break-point(tablet) {
                            font-size: 114px;
                        }
                        @include break-point(desktop) {
                            font-size: rateValue(158px, 0.7);
                        }

                        &:last-child {
                            @include break-point(tablet) {
                                text-indent: 13%;
                            }
                            @include break-point(desktop) {
                                text-indent: 20%;
                            }
                        }
                    }
                }
            }
        }
    }
}
