@import "../utils/variables.scss";
@import "../utils/functions.scss";

body,
html {
    font-family: "Roboto", sans-serif;
    font-size: 16px;
    letter-spacing: normal;
    line-height: normal;
    font-weight: 400;
    font-stretch: normal;
    font-style: normal;
    /* color: black; */

    &:lang(en) {
        font-family: "Roboto", sans-serif;
    }
    &:lang(zh-hant) {
        font-family: "Noto Sans TC", sans-serif;
    }
}

h1,
.text-h1 {
}

h2,
.text-h2 {
    font-size: 90px;
}

h3,
.text-h3 {
    font-size: vw(76px, 750px);
    line-height: 1;
    letter-spacing: vw(-3.8px, 750px);
}

h4,
.text-h4 {
    font-size: rateValue(50px);
    line-height: 1.2;
}

h5,
.text-h5 {
    font-size: vw(42px, 750px);
    line-height: 1.2;
    letter-spacing: vw(-2px, 750px);
}

h6,
.text-h6 {
}

p,
.text-body1 {
    font-size: vw(34px, 750px);
    font-weight: normal;
    line-height: 1.4705882353;
    letter-spacing: normal;
}
.text-body2 {
    font-size: vw(30px, 750px);
    font-weight: 500;
    line-height: 1.6;
    letter-spacing: normal;
}
.text-body3 {
    font-size: vw(28px, 750px);
    font-weight: 500;
    line-height: 1.29;
    letter-spacing: normal;
}

@screen sm {
    h3,
    .text-h3 {
        font-size: 50px;
        line-height: 1;
        letter-spacing: -2.5px;
    }
    h4,
    .text-h4 {
        font-size: 30px;
        line-height: 1;
    }

    h5,
    .text-h5 {
        font-size: rateValue(42px);
        line-height: 1;
        letter-spacing: -2px;
    }
    p,
    .text-body1 {
        font-size: 20px;
        line-height: 1.3;
        letter-spacing: normal;
    }
    .text-body2 {
        font-size: 20px;
        font-weight: 500;
        line-height: 1.2;
        letter-spacing: normal;
    }
    .text-body3 {
        font-size: 20px;
        line-height: 1.29;
    }
}

@screen md {
    h3,
    .text-h3 {
        font-size: rateValue(76px);
        line-height: 1;
        letter-spacing: rateValue(-3.8px);
    }
    h4,
    .text-h4 {
        font-size: rateValue(50px);
        line-height: 1;
    }

    h5,
    .text-h5 {
        font-size: rateValue(42px);
        line-height: 1;
        letter-spacing: -2px;
    }
    p,
    .text-body1 {
        font-size: rateValue(26px);
        line-height: 1.6;
        letter-spacing: normal;
    }
    .text-body2 {
        font-size: rateValue(30px);
        line-height: 1.2;
    }
    .text-body3 {
        font-size: rateValue(24px);
        line-height: 0.79;
        letter-spacing: normal;
    }
}
