@import "../utils/mixins";

%dot {
    @include aspect-ratio(1, 1);
    @apply block;
    @apply rounded-full;
}
.io {
    @apply block;
    width: 100%;
    background-repeat: no-repeat;
    background-size: contain;

    &-black {
        @include aspect-ratio(2642, 58);
        background-image: url(../../assets/images/graphics/io-black.png);
    }
    &-black-short {
        @include aspect-ratio(1313, 58);
        background-image: url(../../assets/images/graphics/io-black-short.png);
    }
    &-white {
        @include aspect-ratio(2642, 58);
        background-image: url(../../assets/images/graphics/io-white.png);
        opacity: 0.2;
    }
    &-white-short {
        @include aspect-ratio(1313, 58);
        background-image: url(../../assets/images/graphics/io-white-short.png);
        opacity: 0.2;
    }
    &-gray {
        @include aspect-ratio(2642, 58);
        background-image: url(../../assets/images/graphics/io-gray.png);
    }
    &-gray-short {
        @include aspect-ratio(1313, 58);
        background-image: url(../../assets/images/graphics/io-gray-short.png);
    }
}
.dot {
    &-primary {
        @extend %dot;
        @apply bg-primary;
    }
    &-yellow {
        @extend %dot;
        @apply bg-yellow-400;
    }
    &-white {
        @extend %dot;
        @apply bg-white;
        border: solid rateValue(4px) #bababa;
    }
    &-black {
        @extend %dot;
        @apply bg-black;
    }
    &-icon {
        &-1 {
            background-image: url(../../assets/images/icons/icon-1@2x.png);
            background-repeat: no-repeat;
            background-position: center center;
            background-size: percentage(124/160) auto;
        }
        &-2 {
            background-image: url(../../assets/images/icons/icon-2@2x.png);
            background-repeat: no-repeat;
            background-position: center center;
            background-size: percentage(128/157) auto;
        }
        &-3 {
            background-image: url(../../assets/images/icons/icon-3@2x.png);
            background-repeat: no-repeat;
            background-position: 56% center;
            background-size: percentage(132/280) auto;
        }
        &-4 {
            background-image: url(../../assets/images/icons/icon-4@2x.png);
            background-repeat: no-repeat;
            background-position: center center;
            background-size: percentage(100/125) auto;
        }
        &-5 {
            background-image: url(../../assets/images/icons/icon-5@2x.png);
            background-repeat: no-repeat;
            background-position: center center;
            background-size: percentage(230/325) auto;
        }
        &-6 {
            background-image: url(../../assets/images/icons/icon-6@2x.png);
            background-repeat: no-repeat;
            background-position: 40% center;
            background-size: percentage(160/294) auto;
        }
        &-leading {
            background-image: url(../../assets/images/icons/icon-leading@2x.png);
            background-repeat: no-repeat;
            background-position: 57% center;
            background-size: percentage(220/460) auto;
        }
    }
