.triangle {
    @apply block;
    @apply w-full;
    @apply absolute;
    overflow: hidden;

    &::after {
        @apply block;
        @apply absolute;
        content: "";
    }
    &.top-left {
        &::after {
            width: 200%;
            height: 200%;
            left: -100%;
            top: 0;
        }
        &.triangle-gray {
            &::after {
                background: linear-gradient(
                        to left bottom,
                        transparent calc(50% - 1px),
                        transparent calc(50% - 1px)
                    ),
                    linear-gradient(
                        to right bottom,
                        #ebebeb calc(50% - 1.5px),
                        transparent calc(50% - 1px)
                    );
            }
        }
        &.triangle-primary {
            &::after {
                background: linear-gradient(
                        to left bottom,
                        transparent 50%,
                        transparent 50%
                    ),
                    linear-gradient(
                        to right bottom,
                        #ff5500 calc(50% - 1px),
                        transparent 50%
                    );
            }
        }
    }
    &.bottom-right {
        &::after {
            width: 200%;
            height: 200%;
            right: -100%;
            top: -100%;
        }
        &.triangle-gray {
            &::after {
                background: linear-gradient(
                        to left top,
                        transparent 50%,
                        white calc(50% - 1px)
                    ),
                    linear-gradient(
                        to right top,
                        #ebebeb calc(50% - 1px),
                        transparent 50%
                    );
            }
        }
        &.triangle-primary {
            &::after {
                background: linear-gradient(
                        to left top,
                        transparent calc(50% - 1px),
                        white calc(50% - 1px)
                    ),
                    linear-gradient(
                        to right top,
                        #ff5500 calc(50% - 1px),
                        transparent calc(50% - 1px)
                    );
            }
        }
        &.triangle-white {
            &::after {
                background: linear-gradient(
                        to left top,
                        transparent calc(50% - 1px),
                        #ebebeb calc(50% - 1px)
                    ),
                    linear-gradient(
                        to right top,
                        white calc(50% - 1px),
                        transparent calc(50% - 1px)
                    );
            }
        }
    }
}
